.body-slider{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.body-slider img{
  width: 55%;
  border-radius: 10px;
}

@media(max-width:997px){
  .body-slider img{
  width: 70%;
}
}
@media(max-width:767px){
  .body-slider img{
  width: 80%;
}
}


